import $ from "cash-dom"
import { Equals } from "./logic.js"

function card(caption, cardValue, onClick) {
	let colour = "Unknown"

	switch (cardValue[0]) {
		case 0:
			colour = "Gold"
			break
		case -1:
			colour = "Red"
			break
		case +1:
			colour = "Blue"
			break
	}

	return `
    <button class="card" onclick="${onClick}">
        <p>${caption}</p>
        <h3>${colour} ${cardValue[1]}</h3>
    </button>
    `
}

function render(Game) {
	let html = ``

	for (const [player, cards] of Object.entries(Game.CARDS_STATE)) {
		html += `<h3>${player}</h3>`

		const output = Game.output(player)
		console.log(output)

		window.stack = (player, data) => {
			Game.interact(player, "stack", data)
		}
		window.deck = (player, data) => {
			Game.interact(player, "deck", data)
		}

		switch (output[0]) {
			case "not_turn_prompt":
				html += "<h2>Not your turn yet.</h2>"
				break
			case "round_prompt":
				html += "<h2>Your turn!</h2>"
				break
			case "start_prompt":
				html += "<h2>Start the game!</h2>"
				html += `<div id="choice">
                    ${card(
						"Stack",
						output[3][0],
						`stack('${player}', ${output[3][0]})`
					)}
                    ${card(
						"Deck",
						output[3][1],
						`deck('${player}', ${output[3][0]})`
					)}
                </div>`
				break
			case "end_prompt":
				html += "<h2>This round ended!</h2>"
				break
			case "spectate_prompt":
				html += "<h2>You're spectating.</h2>"
				break
		}

		for (let c of cards) {
			html += card("", c)
		}
	}

	$("#app").html(html)
}

function main() {
	let Game = new Equals(["Player One", "Player Two"])
	Game.initialize()
	render(Game)

	Game.listen(() => {
        console.log("Change detected.")
		// console.log(Game.message())
		render(Game)
	})
}

$(main())
